.about-wrapper {
  padding: 0;
  min-height: 30em;
  padding-bottom: 2em;
}

.about-wrapper .about-me-col {
  padding: 2em;
}

.about-wrapper .about-me-col .about-me {
  width: 100%;
  padding: 1em;
}

.about-wrapper .about-me-col .about-me .bio {
  margin-top: 1.5em;
  text-align: justify;
  width: 90%;
}

.about-wrapper .about-me-col .about-me .bio .bio-paragraph-1 {
  color: #222222;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
}

.about-wrapper .about-me-col .about-me .bio .bio-paragraph-2 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: #222222;
  line-height: 20px;
}

.about-wrapper .playlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 2em;
}

.about-wrapper .playlist .synth {
  height: 100px;
  width: 100px;
  justify-self: center;
}

@media screen and (max-width: 768px) {
  .about-wrapper .playlist {
    padding-top: 0;
  }
}
